import axiosClient from "./axiosClient";
import queryString from "query-string";

const tableName = "chi-tiet-gom-nhom";
export default {
  getAll: function (filter={}) {
    const query = queryString.stringify({limit:-1,page:0, ...filter });
    const url = `${tableName}?${query}`;
    return axiosClient.get(url);
  },
  getList: function (pageable) {
    let url = `${tableName}?page=${pageable.page - 1}`;
    if (pageable.limit) {
      url += `&limit=${pageable.limit}`;
    }
    if (pageable.sorter.sortBy && pageable.sorter.sortType) {
      url += `&sortBy=${pageable.sorter.sortBy}&sortType=${pageable.sorter.sortType}`;
    }
    if (pageable.searchKey) {
      url += `&search=${pageable.searchKey}`;
    }
    if (pageable.filter.idMH) {
      url += `&idMH=${pageable.filter.idMH}`;
    }
    if (pageable.filter.idGN) {
      url += `&idGN=${pageable.filter.idGN}`;
    }
    return axiosClient.get(url);
  },
  getById: function (id) {
    const url = `${tableName}/${id}`;
    return axiosClient.get(url);
  },
  post: function (body) {
    const url = `${tableName}`;
    return axiosClient.post(url, JSON.stringify(body));
  },
  putById: function (id, body) {
    const url = `${tableName}/${id}`;
    return axiosClient.put(url, JSON.stringify(body));
  },
  deleteById: function (id) {
    const url = `${tableName}/${id}`;
    return axiosClient.delete(url);
  },
  deleteMultiple: function (ids) {
    const url = `${tableName}?ids=${ids}`;
    return axiosClient.delete(url);
  },
  deleteAll: function () {
    const url = `${tableName}/delete/all`;
    return axiosClient.delete(url);
  },
  getAllKHGH: function (idKeHoachGiangDay) {
    const url = `${tableName}/ke-hoach-giang-day​/${idKeHoachGiangDay}`;
    return axiosClient.get(url);
  },
  getAllCTNDT: function (idCTNDT, filter = {}) {
    const query = queryString.stringify({limit:-1,page:0, ...filter });
    const url = `${tableName}/chi-tiet-nganh-dao-tao/${idCTNDT}?${query}`;
    return axiosClient.get(url);
  },
};
