import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { FaFilter } from "react-icons/fa";
import { Button, Form, FormGroup, Input, Label } from "reactstrap";
import { toast } from "react-toastify";

import SubjectAPI from "api/common/subject";
import GroupAPI from "api/common/group";
import SubMajorApi from "api/common/subMajor";
import type_of_knowledge_API from "api/common/type-of-knowledge";
import KnowledgeApi from "api/common/knowledge";

export default function TableFilter({
  currentFilter,
  handleMonHocFilterOnChange,
  handleGomNhomFilterOnChange,
  handleFilterOnClick,
  setPageable
}) {
  const { t } = useTranslation();

  const [monHoc, setMonHoc] = useState([]);
  const [gomNhom, setGomNhom] = useState([]);

  const [lkkt, setLKKT] = useState([]);
  const [subMajorId, setSubMajorId] = useState(null);
  const [listSubMajors, setListSubMajors] = useState([]);
  const [listknowledge, setListknowledge] = useState([]);
  const [knowledgeId, setKnowledgeId] = useState(null);
  // const [listTypeKnowledge, setListTypeKnowledge] = useState([]);
  const [typeKnowledId, setTypeKnowledId] = useState(null);
  // const [groups, setGroups] = useState([]);
  // const [groupId, setGroupId] = useState(null);

  const getListSubMajors = async () => {
    try {
      const listSubMajorRes = await SubMajorApi.getAll();
      if (listSubMajorRes) {
        setListSubMajors(listSubMajorRes.contents);
      }
    } catch (err) {
      setListSubMajors([])
    }
  }
  useEffect(() => {
    (async () => {
      await getListSubMajors();
    })()
  }, [])

  const handleChooseSubMajor = (e) => {
    setSubMajorId(e?.target?.value);
    setKnowledgeId(null);
    setTypeKnowledId(null);
    // setGroupId(null);
  }
  
  useEffect(async () => {
    try {
      let filters = {};
      if(subMajorId){
        filters = {chiTietNganh: subMajorId}
      }
      let listKnowledgeRes = await KnowledgeApi.getAll(filters);
      if (listKnowledgeRes) {
        setListknowledge(listKnowledgeRes.contents);
      }
    } catch (err) {
      setListknowledge([])
    }
  }, [subMajorId]);

  useEffect(async () => {
    try {
      let query = {};
      if(knowledgeId){
        query = {idKhoiKienThuc: knowledgeId}
      }
      const records = await type_of_knowledge_API.getAll(query);
      setLKKT(records.contents);
    } catch (err) {// eslint-disable-next-line
    }
  }, [knowledgeId]);

  useEffect(async () => {
    try {
      let query = {};
      if(typeKnowledId){
        query = {idLKKT: typeKnowledId}
      }
      const recordGomNhom = await GroupAPI.getAll(query);
      setGomNhom(recordGomNhom.contents);
    } catch (err) {// eslint-disable-next-line
    }
  }, [typeKnowledId]);

  useEffect(async () => {
    try {
      const recordMonHoc = await SubjectAPI.getAll({limit: -1});
      setMonHoc(recordMonHoc.contents);
    } catch (err) {// eslint-disable-next-line
    }
  }, []);

  return (
    <Form className="d-flex flex-wrap ">
      <FormGroup style={{width:"fit-content"}}>
        <Label >{t("admin.knowledge_list.SUB_MAJOR")}</Label>
        <Input
          type="select"
          value={subMajorId}
          onChange={handleChooseSubMajor}
        >
          <option value={-1}>{t("")}</option>
          {listSubMajors &&
            listSubMajors.map((item, index) => {
              return (
                <option key={index + 1} value={item.id}>
                  {item.khoa} -{" "} {` ${item.nganhDaoTao?.ten} - ${item?.nganhDaoTao?.chuongTrinhDaoTao?.maCTDT}`}
                </option>
              );
            })}
        </Input>
      </FormGroup>
      &emsp;

      {subMajorId && <><FormGroup style={{width:"fit-content"}}>
        <Label>{t("admin.type_knowledge_list.KNOWLEDGE")}</Label>
        <Input
          type="select"
          value={knowledgeId}
          onChange={e => {setKnowledgeId(e?.target?.value); setTypeKnowledId(null);}}
        >
          <option value={-1}>{""}</option>
          {listknowledge &&
            listknowledge.map((item, index) => {
              return (
                <option key={index + 1} value={item.id}>
                  {/* {item?.chiTietNganh?.khoa}&ensp;{"/"}
                          &ensp;
                  {item?.chiTietNganh?.nganhDaoTao?.ten}&ensp;{"/"}
                          &ensp; */}
                  {item.ten}
                </option>
              );
            })}
        </Input>
      </FormGroup>
      &emsp;</>}

      {knowledgeId ? <FormGroup>
        <Label >{t("admin.group.TYPE_OF_KNOWLEDGE")}</Label>
        <Input
          type="select"
          value={typeKnowledId}
          onChange={e => {setTypeKnowledId(e?.target?.value); setPageable(old => {return {...old, filter: {
            ...old.filter,
            idGN: null,
          }}})}}
        >
          <option value={-1}>{t(" ")}</option>
          {lkkt &&
            lkkt.map((item, index) => {
              return (
                <option key={index} value={item.id}>
                  {item.ten}
                </option>
              );
            })}
        </Input>
      </FormGroup> : ''
      }
      &emsp;
      {typeKnowledId ? <FormGroup>
        <Label for="idGN">{t("admin.detail-group.GROUP")}</Label>
        <Input
          type="select"
          value={currentFilter.idGN !== null ? currentFilter.idGN : -1}
          onChange={handleGomNhomFilterOnChange}
        >
          <option value={-1}>{" "}</option>
          {gomNhom &&
            gomNhom.map((item, index) => {
              return (
                <option key={index} value={item.id}>
                  {`${item?.loaiNhom}${item?.tieuDe ? `-${item?.tieuDe}` : ""}`}
                </option>
              );
            })}
        </Input>
      </FormGroup>: ''}
      &emsp;

      <FormGroup>
        <Label for="idMH">{t("admin.detail-group.SUBJECT")}</Label>
        <Input
          type="select"
          value={currentFilter.idMonHoc !== null ? currentFilter.idMonHoc : -1}
          onChange={handleMonHocFilterOnChange}
        >
          <option value={-1}>{t("admin.common.ALL")}</option>
          {monHoc &&
            monHoc.map((item, index) => {
              return (
                <option key={index} value={item.id}>
                  {item.tenTiengViet}
                </option>
              );
            })}
        </Input>
      </FormGroup>
      &emsp;
      {currentFilter.idGN !== null ?  <FormGroup className="align-self-end">
        <Button onClick={handleFilterOnClick}>
          <FaFilter /> {t("admin.common.FILTER")}
        </Button>
      </FormGroup> : ''}
    </Form>
  );
}
