import React, { useEffect, useState } from "react";
import ReactDOM from "react-dom";
import { useTranslation } from "react-i18next";
import {
  FaAngleDoubleDown,
  FaAngleDoubleUp,
  FaCode,
  FaEnvelope,
  FaLock,
  FaRemoveFormat,
  FaTextWidth,
  FaUserAlt,
  FaUsersCog,
} from "react-icons/fa";
import { toast } from "react-toastify";
import { useHistory, useLocation } from "react-router";
import queryString from "query-string";

import "./DetailGroupList.css";

import { RoutesString } from "pages/routesString";
import { exportHead, exportRow } from "./services/export-csv";
import exportCSVUtil from "utils/export-csv";
import DetailGroupAPI from "api/common/detail-group";
import Breadcrumbs from "components/admin/Breadcrumbs/Breadcrumbs";
import LoadingBar from "components/common/LoadingBar/LoadingBar";
import ConfirmModal from "components/admin/ConfirmModal/ConfirmModal";
import PageHeader from "./components/PageHeader/PageHeader";
import TableFilter from "./components/TableFilter/TableFilter";
import TableHeader from "./components/TableHeader/TableHeader";
import TableTools from "./components/TableTools/TableTools";
import TableFooter from "./components/TableFooter/TableFooter";
import TableBody from "./components/TableBody/TableBody";
import i18n from "i18n/i18n";

export default function DetailGroupList() {
  const { t } = useTranslation();

  const history = useHistory();

  const location = useLocation();

  const [isLoading, setIsLoading] = useState(false);
  const [pageable, setPageable] = useState({
    startPage: 1,
    page: 1,
    limit: 8,
    sorter: {
      sortBy: null,
      sortType: null,
    },
    searchKey: "",
    filter: {
      idMH: null,
      idGN: null,
    },
  });
  const titles = [
    {
      field: "monHoc.tenTiengViet",
      name: "monHoc.tenTiengViet",
      text: t("admin.detail-group.SUBJECT"),
      icon: <FaTextWidth />,
    },
    {
      field: "gomNhom.tieuDe",
      name: "gomNhom.tieuDe",
      text: t("admin.detail-group.GROUP"),
      icon: <FaTextWidth />,
    },
    {
      field: "ghiChu",
      name: "ghiChu",
      text: t("admin.detail-group.NOTE"),
      icon: <FaTextWidth />,
    },
    // {
    //   field: "isDeleted",
    //   name: null,
    //   text: t("admin.common.AVAILABLE_STATUS"),
    //   icon: <FaRemoveFormat />,
    // },
  ];
  const detailTitles = [
    {
      field: "gomNhom.tieuDe",
      text: t("admin.detail-group.GROUP"),
    },
    {
      field: "monHoc.tenTiengViet",
      text: t("admin.detail-group.SUBJECT"),
    },
    {
      field: "ghiChu",
      text: t("admin.detail-group.NOTE"),
    },
    {
      field: "createdAt",
      text: t("admin.common.CREATED_AT"),
      isDate: true
    },
    {
      field: "updatedAt",
      text: t("admin.common.UPDATED_AT"),
      isDate: true
    },
  ];
  const [detailGroups, setDetailGroups] = useState([]);
  const [total, setTotal] = useState(0);
  const [deleteBtnIsHidden, setDeleteBtnIsHidden] = useState(true);
  const [confirmModal, setConfirmModal] = useState(false);
  const confirmToggle = function () {
    setConfirmModal(!confirmModal);
  };

  useEffect(() => {
    const parsed = queryString.parse(window.location.search);
    const pageableParam = {
      startPage: parsed.startPage
        ? parseInt(parsed.startPage)
        : pageable.startPage,
      page: parsed.page ? parseInt(parsed.page) : pageable.page,
      limit: parsed.limit ? parseInt(parsed.limit) : pageable.limit,
      sorter: {
        sortBy: parsed.sortBy ? parsed.sortBy : pageable.sorter.sortBy,
        sortType: parsed.sortType ? parsed.sortType : pageable.sorter.sortType,
      },
      searchKey: parsed.searchKey ? parsed.searchKey : pageable.searchKey,
      filter: {
        idMH: parsed.idMH ? parseInt(parsed.idMH) : pageable.filter.idMH,
        idGN: parsed.idGN ? parseInt(parsed.idGN) : pageable.filter.idGN,
      },
    };
    setPageable(pageableParam);

    updateTable(pageableParam);
  }, [location]);

  const updateTable = async function (currentPageable) {
    setIsLoading(true);
    try {
      const res = await DetailGroupAPI.getList(currentPageable);
      setDetailGroups(res.contents);
      setTotal(res.total);
    } catch (err) {
      if (err.response) {
        if (err?.response?.status === 403 || String(err?.response?.message) === 'NOT_OWNER') {
            toast.error(t("RESPONSE_MESSAGE403"));
          }
        else if (err?.response?.status === 401) {
            toast.error(t("RESPONSE_MESSAGE401"));
        } else if (err?.response?.status === 400) {
            toast.error(t("RESPONSE_MESSAGE400"));
        } else if (err?.response?.status === 409) {
            toast.error(t("RESPONSE_MESSAGE409"));
        } else if (err?.response?.status === 404) {
            toast.error(t("RESPONSE_MESSAGE404"));
        } else {
            toast.error(t("RESPONSE_MESSAGE500"));
        }
    }
    } finally {
      setIsLoading(false);
    }
  };

  const buildUrlWithoutPagination = function (pageable) {
    const url = RoutesString.ADMIN_DETAIL_GROUP_LIST;
    const pageableObj = {
      sortBy: pageable.sorter.sortBy,
      sortType: pageable.sorter.sortType,
      searchKey: pageable.searchKey,
      idMH: pageable.filter.idMH,
      idGN: pageable.filter.idGN,
    };
    const query = queryString.stringify(pageableObj);

    return `${url}?${query}&`;
  };

  const removeCheckAction = function () {
    const rows = document.getElementsByClassName("row-table");
    const checkboxs = document.getElementsByName("row-checkbox");
    setDeleteBtnIsHidden(true);
    for (let i = 0; i < rows.length; i++) {
      rows[i].classList.remove("selected-row");
      checkboxs[i].checked = false;
    }
  };

  const handleMonHocFilterOnChange = function (e) {
    const newPageable = {
      ...pageable,
      startPage: 1,
      page: 1,
      filter: {
        ...pageable.filter,
        idMH: parseInt(e.target.value) === -1 ? null : e.target.value,
      },
    };
    setPageable(newPageable);
  };

  const handleGomNhomFilterOnChange = function (e) {
    const newPageable = {
      ...pageable,
      startPage: 1,
      page: 1,
      filter: {
        ...pageable.filter,
        idGN: parseInt(e.target.value) === -1 ? null : e.target.value,
      },
    };
    setPageable(newPageable);
  };

  const handleFilterOnClick = function () {
    removeCheckAction();
    updateTable(pageable);
  };

  const handleDeleteOnClick = function (e) {
    e.preventDefault();
    confirmToggle();
  };

  const handleOkButtonOnClick = async function (e) {
    e.preventDefault();
    const checkAllBoxes = document.querySelectorAll(
      "input[id=check-all]:checked"
    );
    const rowCheckBoxes = document.querySelectorAll(
      "input[name=row-checkbox]:checked"
    );
    if (checkAllBoxes.length > 0) {
      try {
        setIsLoading(true);
        const res = await DetailGroupAPI.deleteAll();
        updateTable(pageable);
        toast.success(i18n.t("admin.common.DELETED"));
      } catch (err) {
        if (err.response) {
          if (err?.response?.status === 403 || String(err?.response?.message) === 'NOT_OWNER') {
              toast.error(t("RESPONSE_MESSAGE403"));
            }
          else if (err?.response?.status === 401) {
              toast.error(t("RESPONSE_MESSAGE401"));
          } else if (err?.response?.status === 400) {
              toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE400"));
          } else if (err?.response?.status === 409) {
              toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE409"));
          } else if (err?.response?.status === 404) {
              toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE404"));
          } else {
              if (String(err?.response?.data?.error)?.includes("ID_NOT_FOUND")) {
                  toast.error(t("RESPONSE_MESSAGE404"));
              } else {
                  toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE500"));
              }
          }
      }
      } finally {
        confirmToggle();
        setIsLoading(false);
      }
    } else if (rowCheckBoxes.length > 0) {
      let ids = "";
      rowCheckBoxes.forEach((e) => (ids += e.value + ","));
      ids = ids.slice(0, ids.length - 1);
      try {
        setIsLoading(true);
        const res = await DetailGroupAPI.deleteMultiple(ids);
        updateTable(pageable);
        toast.success(i18n.t("admin.common.DELETED"));
      } catch (err) {
        if (err.response) {
          if (err?.response?.status === 403 || String(err?.response?.message) === 'NOT_OWNER') {
              toast.error(t("RESPONSE_MESSAGE403"));
            }
          else if (err?.response?.status === 401) {
              toast.error(t("RESPONSE_MESSAGE401"));
          } else if (err?.response?.status === 400) {
              toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE400"));
          } else if (err?.response?.status === 409) {
              toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE409"));
          } else if (err?.response?.status === 404) {
              toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE404"));
          } else {
              if (String(err?.response?.data?.error)?.includes("ID_NOT_FOUND")) {
                  toast.error(t("RESPONSE_MESSAGE404"));
              } else {
                  toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE500"));
              }
          }
      }
      } finally {
        confirmToggle();
        setIsLoading(false);
      }
    }
    removeCheckAction();
  };

  const handleCancelButtonOnClick = function (e) {
    e.preventDefault();
    confirmToggle();
  };

  const handleExportCSVOnClick = async function () {
    setIsLoading(true);
    try {
      const currentPageable = {
        ...pageable,
        page: 1,
        limit: null,
      };
      const res = await DetailGroupAPI.getList(currentPageable);
      const filename = "Untitle.csv";
      exportCSVUtil(filename, res.contents, exportHead, exportRow);
    } catch (err) {
      toast.error(t("RESPONSE_MESSAGE500"));
    } finally {
      setIsLoading(false);
    }
  };

  const handleRefreshOnClick = function () {
    removeCheckAction();
    const newPageable = {
      startPage: 1,
      page: 1,
      limit: 8,
      sorter: {
        sortBy: null,
        sortType: null,
      },
      searchKey: "",
      filter: {
        idMH: null,
        idGN: null,
      },
    };
    setPageable(newPageable);
    updateTable(newPageable);
  };

  const handleLimitOnChange = function (e) {
    removeCheckAction();
    const newPageable = {
      ...pageable,
      startPage: 1,
      page: 1,
      limit: e.target.value,
    };
    setPageable(newPageable);
    updateTable(newPageable);
  };

  const handleSearchOnChange = function (e) {
    setPageable({ ...pageable, searchKey: e.target.value });
  };

  const handleSearchOnClick = function (e) {
    if (pageable.searchKey) {
      removeCheckAction();
      const newPageable = {
        ...pageable,
        startPage: 1,
        page: 1,
      };
      setPageable(newPageable);
      updateTable(newPageable);
    }
  };

  const handleSortOnClick = function (newSortBy) {
    removeCheckAction();
    let newSortType = "ASC";
    if (newSortBy === pageable.sorter.sortBy) {
      if (pageable.sorter.sortType === "ASC") {
        newSortType = "DESC";
      }
    }
    const newPageable = {
      ...pageable,
      startPage: 1,
      page: 1,
      sorter: {
        sortBy: newSortBy,
        sortType: newSortType,
      },
    };
    setPageable(newPageable);
    updateTable(newPageable);
  };

  const handleCheckItemOnChange = function (id) {
    const row = document.getElementById("row-" + id);
    const checkbox = document.getElementById("checkbox-" + id);
    if (checkbox.checked) {
      setDeleteBtnIsHidden(false);
      row.classList.add("selected-row");
    } else {
      const checkAll = document.getElementById("check-all");
      checkAll.checked = false;
      row.classList.remove("selected-row");

      let isChecked = false;
      const checkboxs = document.getElementsByName("row-checkbox");
      for (let i = 0; i < checkboxs.length; i++) {
        if (checkboxs[i].checked) {
          isChecked = true;
          break;
        }
      }
      if (!isChecked) {
        setDeleteBtnIsHidden(true);
      }
    }
  };

  const handleCheckAllOnChange = function (e) {
    const rows = document.getElementsByClassName("row-table");
    const checkboxs = document.getElementsByName("row-checkbox");
    if (e.target.checked) {
      setDeleteBtnIsHidden(false);
      for (let i = 0; i < rows.length; i++) {
        rows[i].classList.add("selected-row");
        checkboxs[i].checked = true;
      }
    } else {
      setDeleteBtnIsHidden(true);
      for (let i = 0; i < rows.length; i++) {
        rows[i].classList.remove("selected-row");
        checkboxs[i].checked = false;
      }
    }
  };

  const handleEditOnClick = function (id) {
    history.push(`${RoutesString.ADMIN_DETAIL_GROUP_EDIT}?id=${id}`);
  };

  const handleShowDetailOnClick = function (id) {
    const showDetailBtn = document.getElementById("show-detail-" + id);
    const detailRow = document.getElementById("detail-row-" + id);

    if (detailRow.classList.contains("d-none")) {
      detailRow.classList.remove("d-none");
      ReactDOM.render(<FaAngleDoubleUp fill="green" />, showDetailBtn);
    } else {
      detailRow.classList.add("d-none");
      ReactDOM.render(<FaAngleDoubleDown fill="green" />, showDetailBtn);
    }
  };

  return (
    <div className="page">
      {isLoading && <LoadingBar />}

      <Breadcrumbs currentItem={t("admin.detail-group.DETAIL_GROUP_LIST")} />

      <PageHeader
        headerTitle={t("admin.detail-group.DETAIL_GROUP_LIST")}
        addPageUrl={RoutesString.ADMIN_DETAIL_GROUP_EDIT}
      />

      <TableFilter
        currentFilter={pageable.filter}
        handleMonHocFilterOnChange={handleMonHocFilterOnChange}
        handleGomNhomFilterOnChange={handleGomNhomFilterOnChange}
        handleFilterOnClick={handleFilterOnClick}
        setPageable={setPageable}
      />

      <ConfirmModal
        modal={confirmModal}
        toggle={confirmToggle}
        headerTitle={t("admin.common.DELETE_CONFIRM_HEADER")}
        bodyContent={t("admin.common.DELETE_CONFIRM_BODY")}
        handleOkButtonOnClick={handleOkButtonOnClick}
        handleCancelButtonOnClick={handleCancelButtonOnClick}
      />

      <TableHeader
        headerTitle={t("admin.detail-group.DETAIL_GROUP_TABLE")}
        deleteBtnIsHidden={deleteBtnIsHidden}
        handleDeleteOnClick={handleDeleteOnClick}
        handleExportCSVOnClick={handleExportCSVOnClick}
        handleRefreshOnClick={handleRefreshOnClick}
      />

      <TableTools
        currentLimit={pageable.limit}
        currentSearchKey={pageable.searchKey}
        handleLimitOnChange={handleLimitOnChange}
        handleSearchOnChange={handleSearchOnChange}
        handleSearchOnClick={handleSearchOnClick}
      />

      <TableBody
        titles={titles}
        detailTitles={detailTitles}
        items={detailGroups}
        currentSorter={pageable.sorter}
        handleCheckItemOnChange={handleCheckItemOnChange}
        handleCheckAllOnChange={handleCheckAllOnChange}
        handleSortOnClick={handleSortOnClick}
        handleEditOnClick={handleEditOnClick}
        handleShowDetailOnClick={handleShowDetailOnClick}
      />

      <TableFooter
        currentUrl={buildUrlWithoutPagination(pageable)}
        currentStartPage={pageable.startPage}
        currentPage={pageable.page}
        currentLimit={pageable.limit}
        currentTotal={total}
      />
    </div>
  );
}
